import {Component, OnInit} from '@angular/core';
import {PeliculasService, IPelicula} from '../../services/peliculas.service'

@Component({
    selector: 'app-base',
    templateUrl: './base.component.html',
    styleUrls: ['./base.component.css']
})
export class BaseComponent implements OnInit {

    titulo:string;
    peliculas:Array<IPelicula>;
    peliculaDetalle:IPelicula;
    
    constructor(public peliculaService:PeliculasService) {
        this.titulo = "InfoCine";
        this.peliculas = peliculaService.obtenerPeliculas();
        this.peliculaDetalle = this.peliculas[0];
        console.log(this.peliculas);
    }

    ngOnInit() {
      
    }
    
    cambiaDetalle(pelicula:IPelicula){
        this.peliculaDetalle = pelicula;
        console.log("pelicula que viene de app-lista");
        console.log(pelicula);
    }

}



