import { Component, OnInit,Input, Output, EventEmitter} from '@angular/core';
import {IPelicula} from '../../services/peliculas.service';
@Component({
  selector: 'app-lista',
  templateUrl: './lista.component.html',
  styleUrls: ['./lista.component.css']
})
export class ListaComponent implements OnInit {
  @Input() peliculas: Array<IPelicula>;
  @Output() cambiaDetalle =new EventEmitter();
  constructor() { }

  ngOnInit() {
    console.log("en componente listas: ");
    console.log(this.peliculas);
  }

  seleccionarPelicula(pelicula:IPelicula){
    this.cambiaDetalle.emit(pelicula);
    console.log("mostrar pelicula desde lista");
   // console.log(pelicula);
  }

}



